import React, { Component } from 'react'
import axios from 'axios'
import isEqual from 'lodash/isEqual'
import BuilderElement from './BuilderElement/BuilderElement'
import { renderToString } from 'react-dom/server'
import { refinePropertiesFromCMS } from './BuilderElement/methods/refineProperties'

if (process.env.NODE_ENV === 'development') {
    axios.defaults.baseURL = 'http://api.websiter.dev:5000'
} else {
    axios.defaults.baseURL = 'https://api.websiter.dev'
}

axios.defaults.headers.post['Content-Type'] = 'application/json'
axios.defaults.headers.put['Content-Type'] = 'application/json'
axios.defaults.headers.delete['Content-Type'] = 'application/json'
axios.defaults.headers.common.Accept = 'application/json'

class App extends Component {
    mD = {
        resourcesObjects: {},
        page: '',
        template: '',
        structure: [],
        pagesStructure: [],
        pluginsStructure: [],
        baseUrl: '',
        domainNoIndex: false,
    }

    state = {
        loaded: false,
    }

    componentDidMount() {
        return axios
            .post(
                '/api/resources/live',
                JSON.stringify({
                    url: window.location.href,
                })
            )
            .then(response => {
                this.mD = {
                    ...response.data,
                    structure:
                        response.data.resourcesObjects[response.data.template]
                            .structure,
                }
                console.log(this.mD)
                this.setState({ loaded: true })
            })
            .catch(err => {})
    }

    render() {
        const { mD } = this

        const currentPageItemInStructure = mD.pagesStructure.find(
            item => item.id === mD.page
        )
        if (mD.structure.length > 0) {
            const bodyElement = mD.structure.filter(
                item => item.path.length === 1
            )[1]
            const bodyProps = bodyElement.properties
            const bodyStyle = bodyElement.style
            if (bodyStyle) bodyProps.style = bodyStyle
            for (let attr in bodyProps) {
                document.body.setAttribute(attr, bodyProps[attr])
            }

            const htmlElement = mD.structure.filter(
                item => item.path.length === 0
            )[0]
            const htmlProps = htmlElement.properties
            const htmlStyle = htmlElement.style
            if (htmlStyle) htmlProps.style = htmlStyle
            for (let attr in htmlProps) {
                document.documentElement.setAttribute(attr, htmlProps[attr])
            }

            const refinedProperties = refinePropertiesFromCMS({
                pageTemplateDraft: mD.resourcesObjects[mD.template],
                currentPageDraft: mD.resourcesObjects[mD.page],
            })
            const pageResult = mD.structure
                .filter(itemInn => isEqual(itemInn.path, ['element_01']))
                .map((itemInn, index) => {
                    const result = (
                        <BuilderElement
                            key={itemInn.id}
                            structure={mD.structure}
                            element={itemInn}
                            resourceDraft={
                                mD.template
                                    ? mD.resourcesObjects[mD.template]
                                    : {}
                            }
                            currentResource={mD.page}
                            isHead={index === 0}
                            isHeadOrBody={true}
                            pluginsPathArray={[]}
                            pageInStructure={currentPageItemInStructure}
                            parentPluginProps={refinedProperties}
                            mD={mD}
                        />
                    )
                    if (index === 0) {
                        document.head.innerHTML =
                            document.head.innerHTML +
                            (mD.domainNoIndex && mD.baseUrl
                                ? '<meta name="robots" content="noindex">'
                                : ' ') +
                            renderToString(result)

                        return null
                    } else {
                        return result
                    }
                })
            return (
                <>
                    {pageResult}{' '}
                    <div
                        style={{
                            position: 'fixed',
                            width: '100%',
                            height: '50px',
                            background: 'black',
                            color: 'white',
                            bottom: '0px',
                        }}
                    >
                        This website has been built with and is hosted on
                        <a href="https://websiter.dev/">Websiter.dev</a>.{' '}
                        <a href="https://websiter.dev/">
                            Build your website with the power of Websiter.
                        </a>
                    </div>
                </>
            )
        } else {
            return null
        }
    }
}

export default App
